import { h, Component } from 'preact';
import { Router } from 'preact-router';

import Header from './header';
import Photoframe from './photoframe';
import Glasses from './glasses';
import Home from '../routes/home';
import Profile from '../routes/profile';
// import Home from 'async!./home';
// import Profile from 'async!./profile';

export default class App extends Component {

	constructor(){
		super();
		this.state.glass = {};
		this.state.isShow = false;
	};

	componentDidMount() {

		this.video = document.getElementById('video');
		this.canvas = document.getElementById('canvas');
		this.context = this.canvas.getContext('2d');

		this.tracker = new tracking.ObjectTracker('face');
		//this.tracker = new tracking.ObjectTracker([ 'face', 'eye' ]);
		//this.tracker = new tracking.ObjectTracker('eye');
		this.tracker.setInitialScale(4);
		this.tracker.setStepSize(3);
		this.tracker.setEdgesDensity(0.1);

		tracking.track('#video', this.tracker, { camera: true });
	}

	selectGlass = (glass) => {
		this.setState({ "glass": glass});
		this.setState({ isShown: false });

		var glassImg = document.getElementById("glassImg");
		glassImg.style.display = 'block';

		var context = this.context;
		var canvas = this.canvas;
		var video = this.video

		this.tracker.on('track', function(event) {
			context.clearRect(0, 0, canvas.width, canvas.height);

			event.data.forEach(function(rect) {

				//context.drawImage(glassImg, rect.x, rect.y, 200, 150);

				glassImg.style.left = (video.offsetLeft + rect.x + 10 ) + 'px';
				glassImg.style.top = (video.offsetTop + rect.y + 30 ) + 'px';
				var gh = glassImg.style.height;
				var gw = glassImg.style.width;
				var w = rect.width*0.9;
				var h = (gh * (w/gw));
				glassImg.style.height = h + 'px';
				glassImg.style.width = w + 'px';

				/*
				context.strokeStyle = '#a64ceb';
				context.strokeRect(rect.x, rect.y, rect.width, rect.height);
				context.font = '11px Helvetica';
				context.fillStyle = "#fff";
				context.fillText('x: ' + rect.x + 'px', rect.x + rect.width + 5, rect.y + 11);
				context.fillText('y: ' + rect.y + 'px', rect.x + rect.width + 5, rect.y + 22);
				*/

			});
		});

	};

	seeGlasses = () => {
		this.setState({ isShown: !this.state.isShown });
	}

	handleChange = (e) => {
		console.log(e);
		var file = e.target.files[0];
		if ( file ){
			var URL = window.URL || window.webkitURL;
			var fileURL = URL.createObjectURL(file);
			this.video.src = fileURL;
		}
	}

	render(props, state) {
		return (
			<div class="mdl-layout mdl-js-layout" >
				<div id="app" class="mdl-layout--content">
					<Header />
					<div class="mdl-grid">
						<div class="mdl-cell mdl-cell--2-col mdl-cell--hide-tablet mdl-cell--hide-phone" ></div>
						<div class="mdl-shadow--4dp content mdl-cell mdl-cell--8-col">
							<img id="glassImg" src={"assets/glasses/" + this.state.glass.url + ".png" } style="display: none;"/>
							<input type="file" accept="image/*;capture=camera" onChange = { this.handleChange } />
							<Photoframe />
							<button
								onClick = { this.seeGlasses }
								id="btn-show-glasses" class="mdl-button mdl-js-button mdl-button--fab mdl-js-ripple-effect mdl-button--colored">
								<span class="fa fa-shopping-bag"></span>
							</button>
						</div>
					</div>
					<Glasses
						isShown = { this.state.isShown }
						glassSelected = { this.selectGlass }
					/>
				</div>
			</div>
		);
	}
}



