import { h, Component } from 'preact';
import Glass from '../glass';
import style from './style';

export default class Glasses extends Component {
	constructor(){
		super();
		this.state.lstGlasses = [
			{"marca":"Rayban", "model":"XY239","url" :"glass1"},
			{"marca":"CK", "model":"CK441","url":"glass2" },
			{"marca":"Dior", "model":"PM101","url":"glass3" },
			{"marca":"CH", "model":"CH394","url":"glass4" } ];
		this.state.selectedGlass = {};
	};

	isActive = ( glass ) => {
		return this.state.selectedGlass === glass;
	}

	onGlassSelected = (glass) => {
		this.setState({ selectedGlass: glass });
		this.props.glassSelected(glass);
	};

	render(props, state) {
		var lstGlass = state.lstGlasses;
		return (
			<div class={style.glasses_container + ( this.props.isShown ? ' animated fadeInUp' : ' animated fadeOutDown' )}>
				<p>Model Selected: { this.state.selectedGlass.marca } &nbsp; { this.state.selectedGlass.model }</p>
				<div class="mdl-grid">
					{
						lstGlass.map( glass => 
							<Glass item={ glass }
								isActive = { this.isActive(glass) }
								onGlassSelected = { this.onGlassSelected }
							/>
						)
					}
				</div>
			</div>
		);
	}
}



