import { h, Component } from 'preact';
import style from './style';

export default class Glass extends Component {
	constructor(){
		super();
	}

	selectGlass = () => {
		this.props.onGlassSelected(this.props.item);
	}

	render() {
		return (
			<div class="mdl-cell mdl-cell--3-col mdl-cell--12-phone">
				<a href="#" 
					className={ style.img_link + (this.props.isActive ? ' animated pulse' : '') }
					onClick={ this.selectGlass }
					title={ this.props.item.marca + " " + this.props.item.model }>
					<img class={style.img} src={"assets/glasses/" + this.props.item.url + ".png" } />
					<span>{  this.props.item.marca + " " + this.props.item.model } </span>
				</a>
			</div>
		);
	}
}



